              //Read all rows from First Sheet into an JSON array.
              this.$store.state.database.dbALF.data = XLSX.utils.sheet_to_row_object_array(workbook.Sheets[firstSheet]);
              this.$store.state.database.dbALF.archivoRegistros = this.$store.state.database.dbALF.data.length;
              this.$store.state.database.dbALF.data.forEach(prepareData)

              function prepareData(arrItem, index, arr) {
                arrItem.id = index;
                arr[index] = arrItem ;

                arrItem.hasOwnProperty(' aniosadmin') ? arrItem['aniosadmin']=arrItem[' aniosadmin'] : arrItem['aniosadmin']='';
                arrItem.hasOwnProperty(' aniosdoc') ? arrItem['aniosdoc']=arrItem[' aniosdoc'] : arrItem['aniosdoc']='';
