                arrItem.hasOwnProperty(' horasdoc') ? arrItem['horasdoc']=arrItem[' horasdoc'] : arrItem['horasdoc']='';
                arrItem.hasOwnProperty(' nombre') ? arrItem['nombreCompleto']=arrItem[' nombre'] : arrItem['nombreCompleto']='';
                arrItem.hasOwnProperty(' plaza') ? arrItem['numPlaza']=arrItem[' plaza'] : arrItem['numPlaza']='';
                arrItem.hasOwnProperty(' porcenadmin') ? arrItem['porcenadmin']=arrItem[' porcenadmin'] : arrItem['porcenadmin']='';
