                arrItem.hasOwnProperty('nombre categoria') ? arrItem['descCategoria']=arrItem['nombre categoria'] : arrItem['descCategoria']='';
                arrItem.hasOwnProperty('numero empleado') ? arrItem['noEmpleado']=arrItem['numero empleado'] : arrItem['noEmpleado']='';
                arrItem.hasOwnProperty('quincena') ? arrItem['quincena']=arrItem['quincena'] : arrItem['quincena']='';

                 delete arrItem[' aniosadmin'];
